html, body {
    margin: 0;
    padding: 0;
    height: 100%;
  
    background: url(../images/background.jpg) no-repeat center fixed;
    -webkit-background-size: cover;
    background-size: cover; 
    background-attachment: fixed;
    background-position: center center;
    background-color:#464646;
  }
  
  .ground {
    background-color: rgba(57, 31, 22, 0.5);
    height: 100%;
    width: 100%; 
    padding: 5%;
    margin-top: 5%;
  }
  
  .bottom-margin{
    margin-bottom: 20px;
    color: coral;
  }

  .text-color{
    color:#2523C9 !important;
  }

  .right-translate{
    background-color: rgba(21, 112, 21, 0.7);
  }

  .bad-translate{
    background-color: rgba(150, 52, 52, 0.7);
  }


